const DB_NAME = 'apiCache'
const DB_VERSION = 1
const STORE_NAME = 'responses'

const CACHE_CLEANUP_INTERVAL = 24 * 60 * 60 * 1000 // 24 hours
const CACHE_DURATION = 30 * 60 * 1000 // 30 minutes

const openDB = () => {
  return new Promise<IDBDatabase>((resolve, reject) => {
    const request = indexedDB.open(DB_NAME, DB_VERSION)

    request.onupgradeneeded = (event) => {
      const db = (event.target as IDBOpenDBRequest).result
      if (!db.objectStoreNames.contains(STORE_NAME)) {
        db.createObjectStore(STORE_NAME, { keyPath: 'key' })
      }
    }

    request.onsuccess = (event) => {
      resolve((event.target as IDBOpenDBRequest).result)
    }

    request.onerror = (event) => {
      reject((event.target as IDBOpenDBRequest).error)
    }
  })
}

const getTransaction = async (mode: IDBTransactionMode) => {
  const db = await openDB()
  return db.transaction(STORE_NAME, mode).objectStore(STORE_NAME)
}

const serializeHeaders = (headers: Headers): Record<string, string> => {
  const serialized: Record<string, string> = {}
  headers.forEach((value, key) => {
    serialized[key] = value
  })
  return serialized
}

const deserializeHeaders = (headersObj: Record<string, string>) => {
  const headers = new Headers()
  for (const key in headersObj) {
    headers.append(key, headersObj[key])
  }
  return headers
}

const setCache = async (key: string, response: any) => {
  const store = await getTransaction('readwrite')
  const { data, responseHeaders } = response
  store.put({
    key,
    data: {
      ...data,
      responseHeaders: responseHeaders ? serializeHeaders(responseHeaders) : {},
    },
    timestamp: Date.now(),
  })
}

const getCache = async (key: string) => {
  const store = await getTransaction('readonly')
  return new Promise<any>((resolve, reject) => {
    const request = store.get(key)
    request.onsuccess = (event) => {
      const result = (event.target as IDBRequest).result
      if (result) {
        resolve({
          ...result,
          data: {
            ...result.data,
            responseHeaders: deserializeHeaders(result.data.responseHeaders),
          },
        })
      } else {
        resolve(null)
      }
    }
    request.onerror = (event) => {
      reject((event.target as IDBRequest).error)
    }
  })
}

const removeCache = async (key: string) => {
  const store = await getTransaction('readwrite')
  store.delete(key)
}

const cleanUpCache = async () => {
  const store = await getTransaction('readwrite')
  const request = store.openCursor()
  const now = Date.now()

  request.onsuccess = (event) => {
    const cursor = (event.target as IDBRequest<IDBCursorWithValue>).result
    if (cursor) {
      const { timestamp } = cursor.value
      if (now - timestamp > CACHE_DURATION) {
        store.delete(cursor.key)
      }
      cursor.continue()
    }
  }

  request.onerror = (event) => {
    console.error(
      'Failed to clean up expired cache:',
      (event.target as IDBRequest).error
    )
  }
}

const removeCacheEntriesByURL = async (url: string) => {
  const store = await getTransaction('readwrite')

  const request = store.openCursor()

  request.onsuccess = (event) => {
    const cursor = (event.target as IDBRequest<IDBCursorWithValue>).result

    if (cursor) {
      const { url: cachedUrl } = JSON.parse(String(cursor.key))

      const sanitizedCachedUrl = cachedUrl.split('?')[0]

      if (sanitizedCachedUrl === url) {
        cursor.delete()
      }

      cursor.continue()
    }
  }

  request.onerror = (event) => {
    console.error(
      'Failed to delete cache entry:',
      (event.target as IDBRequest).error
    )
  }
}

let cacheCleanupInterval: NodeJS.Timeout

const setupCacheCleanupInterval = () => {
  cacheCleanupInterval = setInterval(() => {
    cleanUpCache().catch(console.error)
  }, CACHE_CLEANUP_INTERVAL)
}

const wipeCache = () => {
  cleanUpCache()
  clearInterval(cacheCleanupInterval)
}

export const ApiCache = {
  set: setCache,
  get: getCache,
  remove: removeCache,
  removeByUrl: removeCacheEntriesByURL,
  cleanup: cleanUpCache,
  wipe: wipeCache,
}

setupCacheCleanupInterval()
