import EventEmitter from 'events'
import { Agent } from 'https'

import { RateLimiter } from 'limiter'

import { ApiCache as ApiIndexDBCache } from './cache'

export type TrackResponse<T> = Promise<{
  data: T
  responseHeaders: Headers
  status: number
  statusText: string
}>

// eslint-disable-next-line @toggl/no-color-literals
type ServiceLevel = 'GREEN' | 'YELLOW' | 'RED' | 'UNKNOWN'

type ApiType = 'accounts' | 'plan' | 'track' | 'reports'

type OptimizationOptions = {
  /**
   * Enables request deduplication to prevent multiple requests for the same
   * resource to happen in parallel.
   *
   * @default true
   */
  dedupe?: boolean
  /**
   * Enables request caching to prevent refetching data that has been recently fetched
   *
   * @default false
   */
  cache?: boolean
  /**
   * The duration in minutes to how long the data should be cached
   *
   * @default 5
   */
  cacheDurationInMinutes?: number
  /**
   * Enables persistent caching in `IndexDB` in cases where user refreshes the page
   *
   * @default false
   */
  persist?: boolean
}

type InitOptions = RequestInit & {
  optimizations?: OptimizationOptions
}

// These variables are static because there are several instances of the API client on webapp
// We won't need to use these once permissions are fully available on production
let resolve: (value?: unknown) => void
const promise = new Promise((_resolve) => {
  resolve = _resolve
})
let useWebappPermissions = false

const requestMap = new Map<string, Promise<any>>()

const cacheStorage = new Map<string, { data: any; timestamp: number }>()

const DEFAULT_OPTIMIZATIONS = {
  dedupe: true,
  cache: false,
  persist: false,
  cacheDurationInMinutes: 5,
} as const

export class Base extends EventEmitter {
  #accountsHost: string
  #agent: Agent | undefined = undefined
  #apiToken: string | undefined
  #blacklist: Set<string> = new Set()
  #headers: Record<string, string>
  #host: string
  #planHost: string
  #reportsHost: string
  #skipPermissionsDependency: boolean
  #teapot = false
  #version: string

  #rateLimiter = new RateLimiter({
    tokensPerInterval: 10,
    interval: 3000,
  })

  serviceLevel: ServiceLevel = 'UNKNOWN'

  constructor({
    accountsHost = 'https://accounts.toggl.com',
    apiToken,
    headers = {},
    host = 'https://api.track.toggl.com',
    https,
    planHost = 'https://api.plan.toggl.com',
    reportsHost = host,
    skipPermissionsDependency = false,
    version = '',
  }: Partial<{
    accountsHost: string
    apiToken: string
    headers: Record<string, string>
    host: string
    https: {
      certificateAuthority: string | Buffer | string[] | Buffer[]
    }
    planHost: string
    reportsHost: string
    skipPermissionsDependency: boolean
    version: string
  }> = {}) {
    super()

    this.#accountsHost = accountsHost
    this.#apiToken = apiToken
    this.#headers = headers ?? {}
    this.#host = host
    this.#planHost = planHost
    this.#reportsHost = reportsHost
    this.#skipPermissionsDependency = skipPermissionsDependency
    this.#version = version

    if (https) {
      if (!Agent) {
        throw new Error('Not running in a Node.js environment')
      }

      if (https.certificateAuthority) {
        this.#agent = new Agent({ ca: https.certificateAuthority })
      }
    }
  }

  // @todo: remove this in the cleanup phase.
  throttle = () => this.#throttle()

  delete<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    return this.#fetchWithCachingAndDeduplication(
      url,
      {
        ...init,
        method: 'DELETE',
      },
      apiType
    )
  }

  get<T>(url: string, init?: InitOptions, apiType?: ApiType): TrackResponse<T> {
    return this.#fetchWithCachingAndDeduplication(
      url,
      {
        ...init,
        method: 'GET',
      },
      apiType
    )
  }

  head<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    return this.#fetch(
      url,
      {
        ...init,
        method: 'HEAD',
      },
      apiType
    )
  }

  patch<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    this.#invalidateCache(url)
    return this.#fetch(
      url,
      {
        ...init,
        method: 'PATCH',
      },
      apiType
    )
  }

  post<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    if (init?.optimizations?.cache !== true) {
      this.#invalidateCache(url)
      return this.#fetch(
        url,
        {
          ...init,
          method: 'POST',
        },
        apiType
      )
    } else {
      return this.#fetchWithCachingAndDeduplication(
        url,
        {
          ...init,
          method: 'POST',
        },
        apiType
      )
    }
  }

  put<T>(url: string, init?: InitOptions, apiType?: ApiType): TrackResponse<T> {
    this.#invalidateCache(url)
    return this.#fetch(
      url,
      {
        ...init,
        method: 'PUT',
      },
      apiType
    )
  }

  async #throttle(): Promise<void> {
    await this.#rateLimiter.removeTokens(1)
  }

  async #fetch<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    let response

    const headers = new Headers({
      ...this.#headers,
      ...(init?.headers
        ? Array.isArray(init.headers)
          ? Object.fromEntries(init.headers)
          : init.headers
        : {}),
    })

    headers.set(
      'X-User-Agent',
      `@toggl/track-api ${process.env.NEXT_API_VERSION}${
        this.#version ? `; ${this.#version}` : ''
      }`
    )

    if (!headers.has('Content-Type') && !(init?.body instanceof FormData)) {
      headers.set('Content-Type', 'application/json')
    }

    if (this.#apiToken) {
      headers.set(
        'Authorization',
        headers.get('Authorization') ??
          `Basic ${btoa(`${this.#apiToken || ''}:api_token`)}`
      )
    }

    // Plan API's CORS configuration doesnt allow these headers
    if (apiType === 'plan') {
      headers.delete('X-User-Agent')
      headers.delete('App-Version')
      headers.delete('X-Toggl-Client')
      headers.delete('Authorization')
    }

    if (this.#teapot) {
      throw new RequestError("I'm a teapot!", {
        url,
        init,
        response,
      })
    }

    const _apiType: ApiType = url.startsWith('/reports/api/v3/')
      ? 'reports'
      : apiType ?? 'track'

    const baseUrl: Record<ApiType, string> = {
      accounts: this.#accountsHost,
      plan: this.#planHost,
      reports: this.#reportsHost,
      track: this.#host,
    }

    const resource = new URL(url, baseUrl[_apiType])

    if (this.#blacklist.has(resource.pathname)) {
      throw new RequestError('blacklist', {
        url,
        init,
        response,
      })
    }

    let retry

    try {
      const fetchOptions = {
        ...init,
        agent: this.#agent,
        headers,
      }

      this.emit('request', resource, fetchOptions)

      do {
        await this.#throttle()

        response = await fetch(resource.toString(), fetchOptions)

        retry = response.status === 429

        if (retry) {
          this.emit('rateLimited', this.#rateLimiter.tokenBucket.content)

          await this.#rateLimiter.removeTokens(
            this.#rateLimiter.tokenBucket.tokensPerInterval
          )
        }
      } while (retry)

      if (response.status === 401) {
        this.#apiToken = undefined
        this.emit('unauthorized')
        throw new RequestError('unauthorized', {
          url,
          init,
          response,
        })
      }

      if (response.status === 403) {
        this.emit('forbidden')
        throw new RequestError('Forbidden', {
          url,
          init,
          response,
        })
      }

      if (response.status === 410) {
        this.#blacklist.add(resource.pathname)
      }

      if (response.status === 418) {
        this.#teapot = true

        this.emit('teapot')

        throw new RequestError("I'm a teapot!", {
          url,
          init,
          response,
        })
      }

      if (!response.ok) {
        const data = await getErrorResponseData(response)
        throw new RequestError(
          typeof data === 'string' ? data : response.statusText,
          {
            init,
            response,
            url,
            data,
          }
        )
      }

      this.serviceLevel =
        (response.headers.get('X-Service-Level') as ServiceLevel) ?? 'UNKNOWN'

      const responseObj = {
        data: (await getResponseData(response)) as T,
        responseHeaders: response.headers,
        status: response.status,
        statusText: response.statusText,
      }

      this.emit('response', url, responseObj, fetchOptions)

      return responseObj
    } catch (err) {
      let error

      if (err instanceof RequestError) {
        error = err
      } else if (err instanceof Error) {
        error = new RequestError(err.message, {
          init,
          response,
          url,
        })
      } else {
        error = new RequestError('failed', {
          init,
          response,
          url,
        })
      }

      this.listenerCount('error') && this.emit('error', error)
      throw error
    }
  }

  async #fetchWithCachingAndDeduplication<T>(
    url: string,
    init?: InitOptions,
    apiType?: ApiType
  ): TrackResponse<T> {
    const { dedupe, cache, cacheDurationInMinutes, persist } = {
      ...DEFAULT_OPTIMIZATIONS,
      ...(init?.optimizations || {}),
    }

    const key = this.#getRequestKey(url, init)
    const cachedResponse = !cache
      ? null
      : persist
      ? await ApiIndexDBCache.get(key)
      : cacheStorage.get(key)

    if (
      cache &&
      cachedResponse &&
      Date.now() - cachedResponse.timestamp < cacheDurationInMinutes * 60 * 1000
    ) {
      return Promise.resolve(cachedResponse.data)
    }

    if (dedupe && requestMap.has(key)) {
      console.warn('⚠️ Duplicate Request', { url, init })
      return requestMap.get(key) as TrackResponse<T>
    }

    const fetchPromise = this.#fetch<T>(url, init, apiType)
    if (dedupe) requestMap.set(key, fetchPromise)

    try {
      const response = await fetchPromise
      if (cache) {
        const setFunction = persist ? ApiIndexDBCache.set : cacheStorage.set
        setFunction(key, { data: response, timestamp: Date.now() })
      }
      return response
    } finally {
      requestMap.delete(key)
    }
  }

  #invalidateCache(url: string) {
    ApiIndexDBCache.removeByUrl(url)
  }

  #getRequestKey(url: string, init?: InitOptions): string {
    return JSON.stringify({ url, init: { ...init, optimizations: undefined } })
  }

  /** Current API token. */
  apiToken() {
    return this.#apiToken
  }

  /** Update the current API token. */
  setApiToken(token?: string) {
    this.#apiToken = token
  }

  // Setter for permissions, we won't need this once permissions are fully available on production
  setPermissions(value: boolean) {
    useWebappPermissions = value
    resolve()
  }

  // Getter for permissions, we won't need this once permissions are fully available on production
  async getPermissions() {
    if (!this.#skipPermissionsDependency) {
      await promise
    }
    return useWebappPermissions ? 'true' : 'false'
  }

  /**
   * Creates a new instance, with identical properties. Useful if you want to
   * reuse client settings.
   */
  newAPI() {
    const next = new Base()
    next.#apiToken = this.#apiToken
    // TODO: Should be a different instance.
    next.#agent = this.#agent
    // TODO: Should be a different instance.
    next.#headers = this.#headers
    next.#host = this.#host
    next.#reportsHost = this.#reportsHost
    next.#version = this.#version
    next.#teapot = this.#teapot
    next.#rateLimiter = new RateLimiter({
      tokensPerInterval: this.#rateLimiter.tokenBucket.tokensPerInterval,
      interval: this.#rateLimiter.tokenBucket.interval,
    })
    return next
  }
}

export class RequestError extends Error {
  name = 'RequestError'

  url: string
  init: InitOptions | void
  response: Response | void
  data: unknown

  constructor(
    message: string,
    options: {
      url: string
      init?: InitOptions
      response?: Response
      data?: unknown
    }
  ) {
    super(message)
    this.url = options.url
    this.init = options.init
    this.response = options.response
    this.data = options.data
  }

  /**
   * Fingerprint for grouping. See
   * https://docs.sentry.io/product/sentry-basics/grouping-and-fingerprints/
   */
  get fingerprint() {
    return [
      this.init?.method,
      getUrlFingerprint(this.url),
      this.response?.status,
    ]
  }
}

/*
 * The role of this method is to return the response data
 */
const getResponseData = (response: Response): Promise<unknown> => {
  const contentType = response.headers.get('content-type') ?? ''

  if (
    /application\/json/.test(contentType) &&
    (response.status === 204 || response.headers.get('content-length') === '0')
  ) {
    return Promise.resolve(null)
  }

  if (/application\/json/.test(contentType)) {
    return response.json()
  }

  if (!contentType || /^text\/(plain|html)|charset=utf-8$/.test(contentType)) {
    return response.text()
  }

  return response.arrayBuffer()
}

/*
 * The role of this method is to return the error response
 * Due to api inconsistencies we need to parse the data to ensure
 * on response type
 * @param {Response} response
 * @return {string} the error message
 */
const getErrorResponseData = async (response: Response): Promise<string> => {
  const contentType = response.headers.get('content-type') || ''

  if (
    /application\/json/.test(contentType) &&
    response.headers.get('content-length') === '0'
  ) {
    return Promise.resolve(
      'There was an error. If this error persists, please contact support.'
    )
  }

  let responseMessage

  if (/application\/json/.test(contentType)) {
    const responseText = await response.text()
    try {
      responseMessage = JSON.parse(responseText)
    } catch (error) {
      return responseText
    }
  } else {
    responseMessage = await response.text()
  }

  return responseMessage
}

export const getUrlFingerprint = (url: string): string =>
  url
    .split('/')
    .map((part) => part.replace(/^\d+$/, ':id'))
    .join('/')
    .replace(/\?.*$/, '')
