import * as Browser from 'webextension-polyfill';

import * as errorReport from '@src/lib/errorReport';

import { DEFAULT_SETTINGS } from './constants';

export class Db {
  static get<T extends keyof typeof DEFAULT_SETTINGS>(
    key: T
  ): Promise<typeof DEFAULT_SETTINGS[T]> {
    return Browser.storage.sync.get(key).then(result => result[key]);
  }

  static set<T extends keyof typeof DEFAULT_SETTINGS>(
    key: T,
    value: typeof DEFAULT_SETTINGS[T]
  ) {
    return Browser.storage.sync
      .set({ [key]: value })

      .catch(e => errorReport.error(`Error attempting to save ${key}`, e))
      .finally(() => errorReport.info(`Saved setting ${key} :`, value));
  }

  static getLocal(key: string) {
    return Browser.storage.local.get(key).then(result => result[key]);
  }

  static setLocal(key: string, value: string) {
    return Browser.storage.local.set({ [key]: value });
  }

  static load(setting: keyof typeof DEFAULT_SETTINGS, defaultValue: any) {
    this.get(setting).then(value => {
      if (typeof value === 'undefined') {
        value = defaultValue;
        this.set(setting, value);
      }
    });
  }

  static loadAll() {
    for (const k in DEFAULT_SETTINGS) {
      if (DEFAULT_SETTINGS[k] !== undefined) {
        this.load(k as any, DEFAULT_SETTINGS[k]);
      }
    }
  }
}
