import {
  TimeEntryRequest,
  TimeEntryConstraints,
  TimeEntryV9,
} from '@toggl/track-types';
import { MessageFrom } from '..';
import { Db } from './db';
import { ensureSameWorkspace, setProject } from './projectUtils';
import storedOrigins from '@toggl/track-extension';

export const extractHostname = (url: string): string => {
  let hostname: string;
  // find & remove protocol (http, ftp, etc.) and get hostname

  if (url.indexOf('//') > -1) {
    hostname = url.split('/')[2];
  } else {
    hostname = url.split('/')[0];
  }

  // find & remove port number
  hostname = hostname.split(':')[0];
  // find & remove "?"
  hostname = hostname.split('?')[0];

  return hostname;
};

export const extractRootDomain = (url: string): string => {
  let domain = extractHostname(url);

  const splitArr = domain.split('.');
  const arrLen = splitArr.length;

  if (arrLen > 2) {
    domain = splitArr[arrLen - 2] + '.' + splitArr[arrLen - 1];

    if (!storedOrigins[domain]) {
      // Handle cases where subdomain is used (like web.any.do (or sub1.sub2.any.do), we remove web from the beginning)
      const origin = splitArr;
      while (origin.length > 0 && !storedOrigins[origin.join('.')]) {
        origin.shift();
      }
      domain = origin.join('.');
    }
  }
  return domain;
};

export const setTETags = async (entry: TimeEntryRequest, from: MessageFrom) => {
  if (from.type === 'contentScript') {
    const TE = entry as TimeEntryRequest & { tag_ids: number[] };

    const autoTagEnabled = await Db.get('enableAutoTagging');

    TE.tag_ids = autoTagEnabled ? TE.tag_ids : [];

    return TE;
  }

  return entry;
};

export const checkUnmetConstraints = (
  teConstraints: TimeEntryConstraints | null | undefined,
  entry: Pick<TimeEntryV9, 'description' | 'project_id' | 'tag_ids' | 'task_id'>
) => {
  const missing: Array<string> = [];

  if (!teConstraints) return missing;

  if (teConstraints?.time_entry_constraints_enabled) {
    if (teConstraints.description_present && !entry.description.length)
      missing.push('description');
    if (teConstraints.project_present && !entry.project_id)
      missing.push('project');
    if (teConstraints.tag_present && !entry.tag_ids?.length)
      missing.push('tag');
    if (teConstraints.task_present && !entry.task_id) missing.push('task');
  }

  return missing;
};

export const sanitizeTE = async (entry: TimeEntryRequest) => {
  let _entry = (await setProject(entry, {
    type: 'popup',
  })) as unknown as TimeEntryV9;
  _entry = await ensureSameWorkspace(entry);

  return _entry;
};

export const track = (event: string, properties?: any) => {
  if (window.heap) window.heap.track(`extension:${event}`, properties);
};

export const isFirefox = () => typeof browser !== 'undefined';
