/* eslint-disable no-console */
import * as Sentry from '@sentry/browser';

import { RequestError } from '@toggl/track-api';
import { CurrentUser } from '@toggl/track-types';

import cache from 'webext-storage-cache';

import { Db } from './db';

export const init = async () => {
  // why not using `enabled`: https://docs.sentry.io/platforms/javascript/configuration/options/#enabled
  const sendErrorReports = await Db.get('sendErrorReports');
  if (process.env.SENTRY_ENABLED === 'true' && sendErrorReports)
    Sentry.init({
      dsn: process.env.SENTRY_DSN,
      environment: process.env.NODE_ENV,
      release: process.env.VERSION,

      beforeSend: async (event, hint) => {
        const user = await cache.get<CurrentUser>('user');

        return {
          ...event,
          user: { id: String(user?.id) },
        };
      },
    });
};

export const debug = (message?: unknown, ...optionalParams: unknown[]) => {
  console.debug(message, optionalParams);
};

export const info = (message?: unknown, ...optionalParams: unknown[]) => {
  console.info(message, optionalParams);
};

export const error = (error: Error) => {
  console.warn(error);
  reportSentry(error);
};

const reportSentry = async (error: Error) => {
  // drop the reporting if the error is a 400s RequestError
  if (
    error instanceof RequestError &&
    error.response &&
    error.response.status >= 400 &&
    error.response.status < 500
  )
    return;

  if (
    process.env.SENTRY_ENABLED !== 'true' ||
    !((await Db.get('sendErrorReports')) as boolean)
  )
    return;

  const user = await cache.get<CurrentUser>('user');

  Sentry.captureException(error, {
    user: {
      id: user?.id.toString(),
    },
  });
};
